.card {
    height: 340px;
    width: 340px;
    background-color: #FF6060;
    position: relative;
    border-radius: 10px;
    background: #FF6060 linear-gradient(transparent 50%, rgba(0,0,0, 0.7));
}

.card-title {
    position: absolute;
    width: 100%;
    height: 50%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    border-radius: 0 0 10px 10px;
    bottom: 0rem;
    left: 0rem;
    background: linear-gradient(transparent 50%, rgba(0,0,0, 0.7));
}


.card-title h2 {
    padding-left: 1rem;
    padding-bottom: 1rem;
    font-size: 18px;
    font-weight: 300;
    color: white;
}

.card img {
    height: 340px;
    width: 340px;
    border-radius: 10px;
    object-fit: cover;
}

figure img:after {
    content: "...";
    background: #FF6060 linear-gradient(transparent 50%, rgba(0,0,0, 0.7));

}

a {
    text-decoration: none;
}

@media (max-width: 599px) {
    .card {
        width: 100%;
        height: 255px;
    }

    .card img {
        width: 100%;
        height: 255px
    }
}
