.gallery {
    background-color: #F7F7F7;
    border-radius: 25px;
    margin-bottom: 2rem;
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    justify-content: center;
    align-items: center;
    padding: 2.5rem;
    }

    @media (max-width: 599px) {
        .gallery {
            background-color: transparent;
            padding: 0rem;
            padding-top: 20px;
        }
    }
